<template>
  <div class="contents-container" style="margin-top:6rem;">
    <div class="contents-container" style="height:10rem;">
      dashboard입니다.
      <br>
    </div>
    <div class="contents-container">
      <div class="contents_item">
        <DoughnutChart  :chartData="doughnutChartData"
                        :chartOptions="doughnutChartOptions"
                        :width="width"
                        :height="height">
        </DoughnutChart>
      </div>
      <div class="contents_item">
        <BarChart   :chartData="barChartData"
                    :chartOptions="barChartOptions"
                    :width="width"
                    :height="height">
        </BarChart>
      </div>
    </div>
  </div>
</template>

<script>
import DoughnutChart from './chart/DoughnutChart.vue';
import keywordCtgrztn from '../assets/keywordCtgrztn.json'; // select get_result_json(28);

import BarChart from './chart/BarChart.vue';
import keywordPlant from '../assets/keywordPlant.json'; // select get_result_json(27);   keyword 기준

export default {
  props: {
  },
  components: {
    DoughnutChart,
    BarChart,
  },
  created() {
    this.setDoughnutChartData();
    this.setBarChartData();
  },
  data() {
    return {
      barChartData: {},
      barChartOptions : {},
      doughnutChartData: {},
      doughnutChartOptions : {},
      width: 0,
      height: 0,
    };
  },
  methods: {
    setDoughnutChartData() {
      var chartDataLabels = [];
      var chartDataDatas = [];
      var chartBackgroundColor = [];

      for(var i = 0; i < keywordCtgrztn.length; i++) {
        chartDataLabels.push(keywordCtgrztn[i].cdtyp_nm);
        chartDataDatas.push(keywordCtgrztn[i].cd_list.length);
        chartBackgroundColor.push(keywordCtgrztn[i].cdtyp_cd_color);
      }
      this.doughnutChartData = {
        labels:chartDataLabels, 
        datasets:[
          {
            label: '키워드분류건수',
            data: chartDataDatas, 
            backgroundColor: chartBackgroundColor,
            // borderColor: [
            //   'rgba(255, 99, 132, 1)',
            //   'rgba(54, 162, 235, 1)'
            // ],
            borderWidth: 1
                  }
                ]
        },
      this.doughnutChartOptions = {
        indexAxis: 'y', //수평차트 만들기
        scales : {
                x: {
                  // beginAtZero: true
                  // min: 0,
                  // max: 100
                }
              },
        responsive : true,
        animation: {
            duration: 0
        },
      }
      // this.width = 50,
      // this.height = 10
    },
    setBarChartData() {
      var chartDataLabels = [];
      var chartDataDatas = [];
      var chartBackgroundColor = [];

      for(var i = 0; i < keywordPlant.length; i++) {
        console.log('keywordPlant[i].plnt_keyword_vw ::: ', keywordPlant[i].plnt_keyword_vw);
        chartDataLabels.push(keywordPlant[i].plnt_keyword_vw);
        chartDataDatas.push(keywordPlant[i].plnt_list.length);
        chartBackgroundColor.push(keywordPlant[i].cdtyp_cd_color);
      }
      this.barChartData = {
        labels:chartDataLabels, 
        datasets:[{
          // barPercentage: 0.5,
          // barThickness: 6,
          // maxBarThickness: 8,
          // minBarLength: 2,

          label: '식물등록건수',
          data:chartDataDatas, 
          backgroundColor: chartBackgroundColor,
          // borderColor: [
          //   'rgba(255, 99, 132, 1)',
          //   'rgba(54, 162, 235, 1)'
          // ],
          borderWidth: 1
        }]
      },
      this.barChartOptions = {
        indexAxis: 'y', //수평차트 만들기
        scales : {
                x: {
                  // beginAtZero: true
                  // min: 0,
                  // max: 100
                },
                y: {
                  // beginAtZero: true
                }
              },
        // responsive : true,
        // animation: {
        //     duration: 0
        // },
        maintainAspectRatio :false   // 비율고정 해제
      }
      // this.width = 400,
      // this.height = 500
    }
  }
}
</script>

